        }
        .publish-serve {
            padding: pxtorem(16px) pxtorem(16px) pxtorem(8px);
            margin-bottom: pxtorem(20px);
            h3 {
                padding: 0 0 pxtorem(8px) pxtorem(10px);
                font-size: pxtorem(17px);
                color: #222;
                line-height: pxtorem(24px);
                position: relative;
                font-weight: 600;
                &::before {
                    content: '';
                    width: pxtorem(4px);
                    height: pxtorem(15px);
                    border-radius: pxtorem(1px);
                    background: #3C78F7;
                    position: absolute;
                    top: pxtorem(4.5px);
                    left: 0;
                }
            }
            textarea {
                display: block;
                width: 100%;
                min-height: pxtorem(44px);
